.projects {
  width: 950px;
  height: 650px;
  background-color: white;
  border-radius: 30px;
  box-shadow: inset 1px 1px 20px 3px rgb(0 0 0 / 50%);
  transition: all 0.9s ease-out;
  transform: translateX(0px);
}
.projectsTransform {
  transition: all 0.9s ease-out;
  transform: translateX(-170px);
  width: 950px;
  height: 650px;
  background-color: white;
  border-radius: 30px;
  box-shadow: inset 1px 1px 20px 3px rgb(0 0 0 / 50%);
}
.containerSliderHidden {
  max-width: 900px;
  height: 550px;
  margin: 0px auto 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  transition: 0.8s;
  display: flex;
  justify-content: center;
}
.containerSlider {
  max-width: 900px;
  height: 550px;
  margin: 0px auto 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  transition: 0.8s;
  display: flex;
  justify-content: center;
}
.img {
  max-width: 650px;
  height: 250px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  display: flex;
  margin: 10px auto;
}

.btnSlideNext {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #f1f1f1;
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  top: 50%;
  right: 20px;
  transform: translateY(60%);
}
.btnSlidePrev {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #f1f1f1;
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  top: 50%;
  left: 20px;
  transform: translateY(60%);
}

.slide {
  position: absolute;
  visibility: hidden;
  transition: opacity ease-in-out 0.4s;
}
.activeAnim {
  visibility: visible;
  position: absolute;
  transition: opacity ease-in-out 0.4s;
}

.btnSlidePrev:hover {
  opacity: 0.8;
}
.btnSlideNext:hover {
  opacity: 0.8;
}
.title {
  font-family: "Oxygen", sans-serif;
  font-weight: 700;
  text-align: center;
}
.headline {
  color: #6d6868;
  width: 110px;
  margin: 16px 10px;
}
.link {
  margin-top: 15px;
  text-decoration: none;
  color: #000;
  position: relative;
  width: 730px;
  font-weight: 500;
}
.textP {
  position: relative;
  width: 730px;
  font-weight: 500;
}

.containerDots {
  position: absolute;
  bottom: 10px;
  left: 50%;
  top: 56%;
  transform: translateX(-50%);
  display: flex;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid #f1f1f1;
  margin: 0 5px;
  background: #3f3142;
  cursor: pointer;
}
.dotActive {
  background: rgb(211, 20, 201);
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid #f1f1f1;
  margin: 0 5px;
  cursor: pointer;
}
.span {
  display: flex;
  align-items: flex-start;
  height: 50px;
}
.container {
  display: flex;
  margin: 5px;
}

.headingContainer {
  background-color: rgb(224, 219, 219);
  width: 100%;
  height: 210px;
  margin-top: 15px;
}
.links{
  position: absolute;
  right: 45px;
  top: 347px
}

@media screen and (max-width: 1200px) {
  .link {
    width: 630px;
  }
  .textP {
    width: 630px;
  }
  .span {
    height: 60px;
    width: 100%;
    justify-content: center;
  }
  .span:nth-of-type(1) {
    height: 45px;
    width: 100%;
    justify-content: center;
  }
  .span:nth-of-type(2) {
    height: 45px;
    width: 100%;
    justify-content: center;
  }
}

@media screen and (max-width: 1050px) {
  .headline {
    width: 70px;
    font-size: 13px;
  }
  .link {
    margin-top: 15px;
    width: 470px;
    font-size: 14px;
  }
  .textP {
    margin-top: 15px;
    width: 470px;
    font-size: 14px;
  }
  .projectsTransform {
    transform: translateX(-120px);
  }
}

@media screen and (max-width: 768px) {
  .containerSliderHidden {
    display: none;
  }
  .projectsTransform {
    position: absolute;
    transform: translateX(0px);
    width: 95%;
  }
  .projects {
    position: absolute;
    transform: translateX(0px);
    width: 350px;
  }
}

@media screen and (max-width: 590px) {
  .headline {
    width: 68px;
    font-size: 12px;
  }
  .link {
    width: 325px;
    font-size: 12px;
  }
  .textP {
    width: 325px;
    font-size: 12px;
  }
  .projects {
    height: 700px;
  }
  .projectsTransform {
    height: 700px;
  }
  .containerSlider {
    height: 580px;
  }
  .headingContainer {
    height: 235px;
  }
  .containerDots {
    top: 54%;
  }
}
@media screen and (max-width: 425px) {
  .projectsTransform {
    width: 100%;
  }
  .headline {
    width: 50px;
    font-size: 9px;
  }
  .link {
    width: 245px;
    font-size: 10px;
    margin-top: 15px;
  }
  .textP {
    width: 245px;
    font-size: 10px;
    margin-top: 15px;
  }
  .span {
    height: 53px;
  }
  .containerSlider {
    width: 95%;
  }
  .projects {
    width: 310px;
  }
}
@media screen and (max-width: 320px) {
  .headline {
    width: 45px;
    font-size: 8px;
  }
  .link {
    width: 215px;
    font-size: 9px;
  }
  .projects {
    width: 100%;
  }
  .textP {
    width: 215px;
    font-size: 9px;
  }
}
